    #ASPECT
\* ------------------------------------ */
/**
 * Enforce an aspect ratio on a responsive image before the page loads. Uses
 * the same trick as responsive videos.
 *
 * [1] Hide any access overflow of the image in case it doesn't quite fit.
 * [2] Padding 100% creates a square by default.
 * [3] Support video content
 */
._3EWPnNcstFnwumHGQpea61 {
  overflow: hidden;
  padding-top: 100%;
  width: 100%;
  position: relative; }
  ._3EWPnNcstFnwumHGQpea61 * {
    display: block;
    margin: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0; }
  ._3EWPnNcstFnwumHGQpea61 iframe,
  ._3EWPnNcstFnwumHGQpea61 embed,
  ._3EWPnNcstFnwumHGQpea61 video {
    height: 100%; }

/**
 * Loop throught the config and build out aditional sizes.
 */
._3kkCDwYAaReB0974H5jU8R {
  padding-top: 56.25%; }

.EkAM26JWSUSghROwCEvw7 {
  padding-top: 62.5%; }

.-KqUyW9QwweP1kVLZr8xB {
  padding-top: 177.86667%; }

.l3IgD6W2czf4GSagD3SnH {
  padding-top: 133.33333%; }
